@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
body {
    background-color: #015A8B;
    font-size: 16px;
    
}
h1, p {
    margin: 0;
}
nav {
    padding-top: 0.77rem;
    margin: 0 3rem 0 5rem;

}
a:focus {
    outline: none;
    outline-offset: 0px;
}

/* Navbar brand & buttons */

.brand { 
    color: #EF7B2B;
    font-family: "Poppins", sans-serif;
    background: transparent;
    font-weight: 700;
    font-size: 1.77rem;
    border: none;
    border-bottom-right-radius: 30px;
    box-shadow: 2px 10px 10px rgba(0, 0, 0, 0.177);
    padding: 0 2rem 0 1rem;
}
.brand-logo {
    margin-top: 3rem ;
    display: inline;
    font-weight: 700;
    font-size: 2.17rem;
}

.action-button {
    color: #eee;
    background: #EF7B2B;
    border: 1px solid #002a41;
    border-radius: 25px;
    font-family: "Poppins", sans-serif;
    font-size: 1.177rem;
    font-weight: 700;
    letter-spacing: 0.1re;
    text-align: center;
    margin-right: 10px;
    padding: 10px 20px;
    cursor: pointer;
    float: right;
    text-shadow: 2px 3px 3px rgba(0, 0, 0, 0.37);
    box-shadow: 1px 7px 7px rgba(0, 0, 0, 0.27);
}
.action-button:hover{ 
    box-shadow: 1px 7px 10px rgba(0, 0, 0, 0.27) inset;
}
.action-button-2{ 
    margin-right: 0;
    background: #F7A51C;
}

/* Flex box container */

.main-container { 
    font-family: "Poppins", sans-serif;
    margin: 0 0 0 5rem;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 5rem;
}

.flex-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: right;
            justify-content: right;
    width: 50%;
}

/* Flex section 1 */

.biggest-heading {
    color: #fff;
    font-family: "Poppins", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 4rem;
    line-height: 70px;
    text-shadow: 3px 10px 10px rgba(0, 0, 0, 0.27);
}
.biggest-heading-orange {
    color: #EF7B2B;
}
.second-heading {
    color: #fff;
    font-family: "Poppins", sans-serif;
    font-weight: 600;
    font-size: 1.2rem;
    margin: 1.77rem 0 0.77rem 0;
    text-shadow: 3px 7px 10px rgba(0, 0, 0, 0.27);
}

/* Form styles */

.form-group {
    width: 29.77rem;
    background: #fff;
    color: #015A8B;
    margin-top: 1.25rem;
    border: 0.3rem solid #fff;
    border-radius: 30rem;
    box-shadow: 1px 7px 7px rgba(0, 0, 0, 0.27);
}
.form-label {
    font-family: "Poppins", sans-serif;
    font-weight: bold;
    font-size: 1.5rem;
    text-align: center;
    padding: 0.5rem 0 0.5rem 0.7rem;
}

.form-input {
    background: #F7A51C;
    color: #fff;
    width: 17.77rem;
    height: 3.277rem;
    border: 0.3rem solid #015A8B;
    border-bottom-right-radius: 30rem;
    border-top-right-radius: 30rem;
    float: right;
    font-size: 1.4rem;
    padding: 0 4px;
    caret-color: #015A8B;
}

.form-group-2 {
    width: 24rem;
}
.form-input-2 {
    width: 12rem;
}

.submit-btn {
    color: #F7A51C;
    background: #fff;
    border: 0.3rem solid #fff;
    border-radius: 30rem;
    font-family: "Poppins", sans-serif;
    font-weight: bold;
    font-size: 1.5rem;
    text-align: left;
    width: 10rem;
    cursor: pointer;
    margin-top: 1.25rem;
    padding: 0.5rem 0 0.5rem 0.7rem;
    box-shadow: 1px 7px 7px rgba(0, 0, 0, 0.27);
}
.submit-btn:hover{ 
    box-shadow: 1px 7px 10px rgba(217, 217, 217, 0.77) inset;
}

.triangle {
    color: #F7A51C;
    display: inline;
    font-size: 1rem;
}
  * {
